export class LoggedInUser {
  id:number;
  fullName: string;
  email: string;
  role: string;
  isAdmin: boolean;


  constructor(id:number,firstName: string, lastName: string, role: string, isAdmin:boolean) {
    this.id = id;
    this.fullName = firstName;
    this.email = lastName;
    this.role = role;
    this.isAdmin = isAdmin;
  }

}
