import { Component, OnInit } from '@angular/core';
import { LoggedInUser } from 'src/app/models/loggedInUser';

// services
import { AuthService } from 'src/app/services/auth.service';
import { AppConfig } from 'src/app/helper/app-config';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { HttpService } from 'src/app/services/http/http.service';
import { Observable } from 'rxjs/internal/Observable';
import { ResultDetails } from 'src/app/models/resultDetails';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {

  mainLogo = AppConfig.logo.toString();

  slides = [
    {title: "The Leading Buy Now Pay Later Solution", image: "assets/images/products.png", hasAction: true, actionName: "Read More"},
    {title: "The Leading Buy Now Pay Later Solution 2", image: "assets/images/products.png", hasAction: false},
    {title: "The Leading Buy Now Pay Later Solution 3", image: "assets/images/products.png", hasAction: false },
  ];

  slideConfig = {
    "slidesToShow": 1,
    "slidesToScroll": 1,
    "dots": true,
    "infinite": false,
    "arrows": false
  };

  validationErrorMessages = {
    "userName": [
      { type: "required", message: "User name is required" }
    ],
    "password": [
      { type: "required", message: "Password is required" },
      { type: "minlength", message: "Password must be at least 6 characters long" }
    ]
  };


  loginFormGroup: FormGroup;

  constructor(private authService: AuthService, private formBuilder: FormBuilder, private httpService: HttpService) { }

  ngOnInit() {
    this.prepareForm();
  }

  prepareForm() {
    this.loginFormGroup = this.formBuilder.group({
      userName: ['', Validators.required],
      password: ['', [Validators.required, Validators.minLength(6)]]
    });
  }
  get f() { return this.loginFormGroup.controls; }

  checkUserNameAndPassword(loginRequset: any): Observable<ResultDetails<LoggedInUser>> {
    return this.httpService.post("login", loginRequset);
  }

  onSubmit() {
    const loginRequset = {
      'UserName':  this.loginFormGroup.controls['userName'].value,
      'Password': this.loginFormGroup.controls['password'].value
    }

    this.checkUserNameAndPassword(loginRequset).subscribe(res=> {
      debugger
      if (res.isSuccess){
        let data = res.data;
        const userInfo: LoggedInUser = new LoggedInUser(data.id,data.fullName,data.email,data.role,data.isAdmin);
        this.authService.login(userInfo);
      } else {
        console.log(res.message)
      }
    })


  }

  handlePageClick(page) {
    console.log({page});
  }
}
